import React,{useState,useEffect} from 'react'
import './Navbar.css'
import axios from '../../axios'
import {API_KEY} from '../../constants/constants'
function Navbar() {

    const [search, setSearch] = useState('')
    const [searchmovie, setSearchmovie] = useState([])

     function Search(){
        axios.get(`/search/movie?api_key=${API_KEY}&language=en-US&page=1&query=${search}`).then((response)=>{
            setSearchmovie(response.data.results[0])
            console.log(searchmovie)

        })
       
     }

    return (
        <div className="navbar">
            <img className="logo" src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/1920px-Netflix_2015_logo.svg.png" alt="oopss" />
            <input onChange ={(e)=>setSearch(e.target.value)} className="ultsm" type="text" placeholder="UNLIMITED TV SHOWS & MOVIES" />
            <button className="buttonjoin">JOIN NOW</button>
            <button className="buttonsign">SIGN IN</button>

            
        </div>
    )
}

export default Navbar
